@import url('https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins');
@import url('https://fonts.googleapis.com/css2?family=Akronim&display=swap');

* {
    margin    : 0;
    padding   : 0;
    box-sizing: border-box;
}

*::selection {
    background: rgba(255, 30, 0, 0.2);
}

html {
    text-transform: unset;
    font-size     : 88%;
    color         : #707070;
    overflow-x    : hidden;
    letter-spacing: 1.2px;
    font-family   : 'Poppins', sans-serif;
}

a {
    text-decoration: none;
    color          : black;
}

#home::before {
    content         : "";
    position        : absolute;
    height          : 2000px;
    width           : 2000px;
    top             : -10%;
    right           : 48%;
    transform       : translateY(-50%);
    background-image: linear-gradient(-45deg, #fff 0%, orangered 150%);
    transition      : 1.8s ease-in-out;
    border-radius   : 50%;
    z-index         : -1;
    overflow        : hidden;
}

button {
    background   : orangered;
    border       : none;
    border-radius: 59.5px;
    color        : white;
    cursor       : pointer;
}

.text {
    position  : relative;
    top       : 350px;
    text-align: center;
    font-size : 40px;
    color     : #12192c;
}

.para {
    margin-left: 3rem;
    width      : 30%;
    color      : #525252;
    font-size  : 1rem;
    margin-top : -5rem;
}

.para span {
    color: orangered;
}

.para h1 {
    font-size  : 3rem;
    font-weight: 700;
}

.para p {
    margin-top : 1rem;
    font-weight: 400;
}

.btn {
    margin-top: 1rem;
}

.btn button {
    height        : 2.5rem;
    width         : 9rem;
    border-radius : 5px;
    text-align    : center;
    text-transform: capitalize;
    font-weight   : 500;
    color         : white;
    background    : #000;
}

.btn button:nth-child(1):hover {
    background: rgba(255, 255, 255, 0.4);
    color     : black;
    border    : 1.5px solid black;
    transition: .5s ease-in-out;
}

.btn button:nth-child(2) {
    background : none;
    color      : orangered;
    border     : 1.5px solid orangered;
    margin-left: 1rem;
}

.btn button:nth-child(2):hover {
    background: orangered;
    color     : white;
    transition: .5s ease-in-out;
}

.animation img {
    margin-left: 121rem;
}

.animation .ogl-img {
    position   : absolute;
    height     : 60%;
    width      : 60%;
    margin-top : -25rem;
    margin-left: 40%;
}

.active {
    color: orangered;
}

.service-text h1 {
    text-align    : center;
    text-transform: capitalize;
}

.service-text h3 {
    text-align    : center;
    text-transform: uppercase;
    color         : orangered;
    margin-top    : 10px;
}

.p-card {
    display              : grid;
    margin               : 100px 90px auto;
    grid-gap             : 20px;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    z-index              : -1;
}

.p-card .box {
    width      : 350px;
    margin     : 0 auto;
    position   : relative;
    perspective: 1000px;
    text-align : center;
}

.p-card .box .front-face {
    background     : #fff;
    height         : 220px;
    width          : 100%;
    display        : flex;
    flex-direction : column;
    justify-content: center;
    box-shadow     : 0px 5px 20px 0px rgba(0, 0, 0, 0.5);
    transition     : all 0.5s ease;
}

.box .front-face .icon {
    height: 80px;
}

.box .front-face .icon i {
    font-size: 65px;
    color    : orangered;
}

.box .front-face span,
.box .back-face span {
    font-size     : 22px;
    font-weight   : 600;
    text-transform: capitalize;
}

.box .front-face span {
    color: black;
}

.box .back-face {
    position           : absolute;
    top                : 0;
    left               : 0;
    z-index            : 1;
    height             : 220px;
    width              : 100%;
    padding            : 30px;
    color              : #fff;
    opacity            : 0;
    transform-style    : preserve-3d;
    backface-visibility: hidden;
    background         : #000;
    transform          : translateY(110px) rotateX(-90deg);
    box-shadow         : 0px 5px 20px 0px rgba(0, 81, 250, 0.1);
    transition         : all 0.5s ease;
}

.box .back-face p {
    color     : white;
    margin-top: 10px;
    text-align: justify;
}

.box:hover .back-face {
    opacity  : 1;
    transform: rotateX(0deg);
}

.box:hover .front-face {
    opacity  : 0;
    transform: translateY(-110px) rotateX(90deg);
}

.partners h2 {
    background             : linear-gradient(-235deg, blue 0%, red 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-align             : center;
    margin-top             : 1rem;
}

.services {
    width          : 100%;
    height         : 100vh;
    display        : flex;
    flex-direction : column;
    justify-content: space-evenly;
    align-items    : center;
}

.s-heading {
    text-align : center;
    padding-top: 5rem;
}

.s-heading h1 {
    color         : #576975;
    font-size     : 3rem;
    font-weight   : 400;
    letter-spacing: 1px;
}

.s-heading span {
    color: orangered;
}

.s-box-container {
    width          : 100%;
    display        : flex;
    justify-content: center;
    align-items    : center;
}

.s-box {
    display        : flex;
    flex-direction : column;
    justify-content: center;
    align-items    : center;
    border-radius  : 10px;
    width          : 300px;
    text-transform : capitalize;
    padding        : 20px 25px;
    height         : 200px;
    box-sizing     : border-box;
    margin         : 30px;
    position       : relative;
}

.s-box img {
    height: 75px;
}

.s-box h1 {
    color         : #576975;
    letter-spacing: 1px;
    font-size     : 1.5rem;
    margin-bottom : 8px;
}

.s-box p {
    color     : rgba(87, 105, 117, 0.90);
    text-align: center;
}

.bar {
    width           : 100px;
    height          : 6px;
    position        : absolute;
    left            : 50%;
    top             : 0%;
    transform       : translateX(-50%);
    background-color: #4a90e2;
    border-radius   : 0px 0px 10px 10px;
    display         : none;
    animation       : bar 0.5s;
}

.s-box:hover {
    box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.08);
    transition: all ease 0.3s;
}

.s-btn:hover {
    background-color: #4a90e2;
    border          : 1px solid #4a90e2;
    color           : #FFFFFF;
    transition      : all ease 0.3s;
}

.s-box:hover .bar {
    display: block;
}

@keyframes bar {
    0% {
        width: 0px;
    }

    100% {
        width: 100px;
    }
}

#more {
    display: none;
}

#facility {
    text-align   : center;
    margin-top   : 10rem;
    margin-bottom: 5rem;
}

#facility h2 {
    text-transform: uppercase;
}

#facility .fa-check-circle {
    color: green;
}

.row {
    display   : flex;
    margin-top: 2rem;
    text-align: center;
}

.row ul li {
    list-style: none;
    text-align: left;
}

.list {
    display: flex;
}

.list-un {
    margin-left: 5rem;
}

.f-col {
    column-count: 2;
}

.s-col {
    margin-left : 5rem;
    column-count: 2;
}

#about {
    background   : rgb(248, 248, 248);
    height       : auto;
    padding-top  : 5rem;
    width        : 100%;
    padding-left : 10%;
    padding-right: 10%;
}

#about h2 {
    text-align : center;
    padding-top: 2rem;
}

#about span {
    color: orangered;
}

#about p {
    text-align    : justify;
    margin-top    : 1rem;
    padding-bottom: 2rem;
}

.about-text p::first-letter {
    font-size  : 2rem;
    font-weight: 600;
}

.background {
    display   : flex;
    min-height: 100vh;
}

.form {
    flex   : 0 1 700px;
    margin : auto;
    padding: 10px;
}

.screen {
    position     : relative;
    background   : #e5e5e5;
    border-radius: 15px;
}

.screen:after {
    content      : '';
    display      : block;
    position     : absolute;
    top          : 0;
    left         : 20px;
    right        : 20px;
    bottom       : 0;
    border-radius: 15px;
    box-shadow   : 0 20px 40px rgba(0, 0, 0, .4);
    z-index      : -1;
}

.screen-header {
    display                : flex;
    align-items            : center;
    padding                : 10px 20px;
    background             : #ddd;
    border-top-left-radius : 15px;
    border-top-right-radius: 15px;
}

.screen-header-left {
    margin-right: auto;
}

.screen-header-button {
    display      : inline-block;
    width        : 8px;
    height       : 8px;
    margin-right : 3px;
    border-radius: 8px;
    background   : white;
}

.fa-circle:nth-child(1) {
    color: #ea1d6f;
}

.fa-circle:nth-child(2) {
    color: #e8e925;
}

.fa-circle:nth-child(3) {
    color: #74c54f;
}

.screen-header-right {
    display: flex;
}

.screen-header-right h3 {
    color     : #999;
    margin-top: -5px;
}

.screen-header-ellipsis {
    width        : 3px;
    height       : 3px;
    margin-left  : 2px;
    border-radius: 8px;
    background   : #999;
}

.screen-body {
    display: flex;
}

.screen-body-item {
    flex   : 1;
    padding: 50px;
}

.screen-body-item.left {
    display       : flex;
    flex-direction: column;
}

.app-title {
    display       : flex;
    flex-direction: column;
    position      : relative;
    color         : orangered;
    font-size     : 26px;
}

.app-title:after {
    content   : '';
    display   : block;
    position  : absolute;
    left      : 0;
    bottom    : -10px;
    width     : 25px;
    height    : 4px;
    background: orangered;
}

.app-contact {
    margin-top: auto;
    font-size : 8px;
    color     : #888;
}

.app-form-group {
    margin-bottom: 15px;
}

.app-form-group.message {
    margin-top: 40px;
}

.app-form-group.buttons {
    margin-bottom: 0;
    text-align   : right;
}

.app-form-control {
    width         : 100%;
    padding       : 10px 0;
    background    : none;
    border        : none;
    border-bottom : 1px solid #666;
    color         : #000;
    font-size     : 14px;
    text-transform: unset;
    outline       : none;
    transition    : border-color .2s;
}

.app-form-control::placeholder {
    color: #666;
}

.app-form-control:focus {
    border-bottom-color: forestgreen;
    animation          : input-anime .5s ease-in-out alternate;
    transition         : 1s step-start;
}

@keyframes input-anime {
    0% {
        border-bottom-color: orangered;
    }

    50% {
        border-bottom-color: darkred;
    }

    100% {
        border-bottom-color: forestgreen;
    }
}

.app-form-button {
    background: none;
    border    : none;
    color     : forestgreen;
    font-size : 14px;
    cursor    : pointer;
    outline   : none;
}

.app-form-button:hover {
    color: orangered;
}

.footer {
    display         : flex;
    flex-flow       : row wrap;
    padding         : 30px 30px 20px 60px;
    color           : #2f2f2f;
    background-color: #fff;
    border-top      : 1px solid #e5e5e5;
    width           : 100%;
}

.footer>* {
    flex: 1 100%;
}

.footer__addr {
    margin-right : 1.25em;
    margin-bottom: 2em;
}

.footer__logo {
    font-family   : 'Pacifico', cursive;
    font-weight   : 400;
    text-transform: lowercase;
    font-size     : 1.5rem;
}

.footer__addr h2 {
    margin-top : 1.3em;
    font-size  : 15px;
    font-weight: 400;
}

.nav__title {
    font-weight: 400;
    font-size  : 15px;
}

.footer address {
    font-style: normal;
    color     : #999;
}

.footer__btn {
    display         : flex;
    align-items     : center;
    justify-content : center;
    height          : 36px;
    max-width       : max-content;
    background-color: rgb(33, 33, 33, 0.07);
    border-radius   : 100px;
    color           : #2f2f2f;
    line-height     : 0;
    margin          : 0.6em 0;
    font-size       : 1rem;
    padding         : 0 1.3em;
}

.footer ul {
    list-style  : none;
    padding-left: 0;
}

.footer li {
    line-height: 2em;
}

.footer a {
    text-decoration: none;
}

.footer__nav {
    display: flex;
}

.footer__nav>* {
    flex        : 1 50%;
    margin-right: 1.25em;
}

.nav__ul a {
    color: #999;
}

.nav__ul--extra {
    column-count: 2;
    column-gap  : 1.25em;
}

.copyright {
    text-align: center;
    margin-top: 10px;
    width     : 100%;
}

#count-bar {
    background   : #131418;
    height       : 7rem;
    width        : 60%;
    margin-top   : 2rem;
    border-radius: 0px 25px 25px 0px;
    position     : absolute;
    left         : 0;
}

.row-bar {
    display       : flex;
    text-align    : center;
    margin-top    : 1.1rem;
    float         : right;
    text-transform: capitalize;
}

.petents {
    margin-right: 3rem;
}

.experience {
    margin-right: 3rem;
}

.verify {
    margin-right: 3rem;
}

#count-bar h2,
#count-bar p {
    color: #fff;
}

#highlight {
    margin-top: 10rem;
}

.special-card {
    width          : 100%;
    display        : flex;
    justify-content: center;
    align-items    : center;
    text-align     : center;
}

.sp-card {
    display        : flex;
    flex-direction : column;
    justify-content: center;
    align-items    : center;
    border-radius  : 10px;
    width          : 300px;
    text-transform : capitalize;
    padding        : 20px 25px;
    height         : auto;
    box-sizing     : border-box;
    margin         : 30px;
    background     : #ddd;
    position       : relative;
}

address {
    text-align   : center;
    margin-bottom: 1rem;
}

.fa-map-marked-alt {
    color    : black;
    font-size: 2rem;
}

#container {
    max-width : 600px;
    margin    : 0 auto;
    text-align: center;
}

.clearfix {
    clear: both;
}

.hidden {
    display: none;
}

#user-info {
    border    : none;
    clear     : both;
    margin    : 0 auto 20px;
    max-width : 400px;
    padding   : 10px;
    text-align: left;
}

#sign-out {
    background   : white;
    color        : #000;
    border       : 1px solid black;
    height       : auto;
    width        : auto;
    font-weight  : 600;
    border-radius: 2px;
    padding      : 5px;
    box-shadow   : 1px 1px 3px black;
}

.home a {
    text-decoration: underline;
}

